/*** CHECKLIST ***/

.checklist {
/*** TEMP SETTING FOR TESTING/DEV PURPOSES***/
background-color: $gray;
}

.checklist__heading {
    display: flex;
    justify-content: space-between;
}

.checklist__title {
    font-size: $standard-text;
}

.checklist__items {
    list-style: none;
    padding: 0;
    text-align: left;
}

.checklist__item {
    display: flex;
    align-items: center;
}

.checklist__complete {
    border: 1px solid $slate-gray;
    width: 20px;
    height: 20px;
    margin: 0 $menu-horizontal;
}

.checklist__name {
    font-size: $standard-text;
    display: inline-block;
    padding: 0;
    margin: 0;
}

.add-new-checkList__placeholder {
    font-size: $standard-text;
    margin: $menu-horizontal;
    cursor: pointer;
}

.progress-bar {

}

.progress-bar__inner {
    width: 0%;
    background-color:$light-green;
    transition: 0.5s all;
}

.btn--checklist-delete {
    margin-left: auto;
    color: $slate-gray;
}